@media(max-width: 930px){
  .todo-list{
    width: 95%;
  }
}

.boton-rojo{
  background-color: rgb(112, 110, 110);
}

.alert-error{
  color: red;
  font-size: 1.3rem;
  text-align: center;
  height: 5vh;
}